/*Eric Meyer reset*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
/* =============================

    GENERAL STYLES

=============================*/

* {
  box-sizing: border-box;
}

/* Single-direction margin declaration to establish vertical rhythm. */
h1,
h2,
h3,
ul,
ol,
p,
img {
  margin-bottom: 1.5rem;
}

body {
  /* background: url("../images/wedding-landing-background.png") repeat center; */
  font-family: "Libre Baskerville", serif;
  font-size: 1.125rem;
  line-height: 1.5;
  color: #444349;
}

.locations,
.program,
.gifts,
.faq,
.contact {
  width: 95%;
  margin: 1.5rem auto;
  padding: 0.5rem 1rem;
  text-align: center;
}

h1,
h2 {
  font-family: "Great Vibes", cursive;
}

h2 {
  font-size: 3rem;
  color: rgb(133, 209, 234);
}
.kannada2 {
  font-weight: bold;
  color: rgb(133, 209, 234);
  background: linear-gradient(to right, #25b9ea 0%, #fdfd22 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.kannada {
  font-size: 2rem;
  font-weight: bold;
  color: rgb(133, 209, 234);
  background: linear-gradient(to right, #25b9ea 0%, #fdfd22 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

h3 {
  font-size: 1.5rem;
}

a {
  text-decoration: none;
  color: #bd806e;
}

a:hover {
  color: #eda087;
  transition: color 0.15s ease-in-out;
}

/* 
 HEADER STYLES*/

.cover {
  background: url("https://orange.codeberryschool.com/content/images/project-assets/wedding-landing-cover.jpg")
    no-repeat 70% center/cover;
  display: table;
  text-align: center;
  height: 85vh;
  width: 100vw;
}

.hero-text-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.hero-text {
  margin: 0 auto;
  padding: 0.625rem;
  color: hsla(0, 0%, 100%, 0.9);
  background: hsla(100, 0%, 0%, 0.5);
}

.hero-text h1 {
  font-size: 3.15rem;
  color: rgb(240, 188, 188);
  margin-bottom: 0; /* To negate the effect of the single-direction margin declaration. */
}

.tagline {
  font-size: 1.25rem;
  letter-spacing: 0.15rem;
}

.tagline::after {
  display: block;
  content: "";
  background-color: rgb(240, 188, 188);
  height: 1px;
  width: 12.515625rem;
  margin: 0.5rem auto;
}

.date {
  font-size: 1.25rem;
  letter-spacing: 0.15rem;
  vertical-align: middle;
}

.date::before,
.date::after {
  display: inline-block;
  content: "";
  background-color: rgb(247, 187, 187);
  height: 1px;
  width: 1.5rem;
  vertical-align: middle;
  margin: 0 0.5rem;
}

/* =============================

      LOCATIONS STYLES

=============================*/

.blurb img {
  height: 7.5rem;
}

.blurb h3 {
  font-weight: 700;
}

.blurb div {
  font-family: "Muli", sans-serif;
}

.blurb::after {
  content: "";
  display: block;
  height: 0.1875rem;
  width: 25vw;
  margin: 1.5rem auto;
  background-color: #444349;
}

@media (min-width: 768px) {
  .blurb {
    display: inline-block;
    width: 30%;
    padding: 1.5rem;
    margin: 0.5rem;
  }

  .blurb::after {
    display: none;
  }
}

/* =============================

        PROGRAM STYLES

=============================*/

.program {
  width: 100%;
  text-align: left;
  background: rgba(32, 32, 32, 0.6);
  color: #f5e1b6;
  font-family: "Muli", sans-serif;
}

.program h2 {
  font-family: "Great Vibes", cursive;
  margin-top: 1rem;
  text-align: center;
}

.program p {
  font-style: italic;
}

.program ul {
  letter-spacing: 0.025rem;
}

@media (min-width: 768px) {
  .program-wrapper {
    width: 30vw;
    margin: 0 auto;
  }
}

/* =============================

        GIFTS STYLES

=============================*/

.gifts img {
  height: 7.5rem;
}

.gifts p:last-child {
  /* To even out the bottom and top margins of this section. */
  margin-bottom: 0;
}

/* =============================

        FAQ STYLES

=============================*/

.faq {
  background: rgba(32, 32, 32, 0.6);
  color: #f5e1b6;
  width: 100%;
  text-align: center;
}

.faq h2 {
  margin-top: 1rem;
}

.faq p {
  font-family: "Muli", sans-serif;
  text-align: left;
}

.question-group::after {
  content: "";
  display: block;
  height: 0.1875rem;
  width: 50vw;
  margin: 1.5rem auto;
  background-color: #d2ba87;
}

@media (min-width: 768px) {
  .question-group {
    display: inline-block;
    width: 30%;
    vertical-align: top;
    padding: 1.5rem;
    margin: 0.5rem;
  }

  .question-group::after {
    display: none;
  }

  .question-group h3 {
    text-align: left;
  }
}

.question {
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.answer {
  margin-bottom: 1.5rem;
}

/* =============================

        FOOTER STYLES

=============================*/

footer {
  height: 10vh;
  width: 100%;
  background-color: #606060;
  color: #fff;
  font-family: "Muli", sans-serif;
  display: table;
  text-align: center;
  font-size: 0.875rem;
}

.footer-wrapper {
  display: table-cell;
  vertical-align: middle;
  padding: 0.5rem 1rem;
}

footer p {
  margin-bottom: 0;
}

footer small {
  line-height: 1.5;
}

@media (min-width: 768px) {
  footer {
    min-height: 7vh;
  }
}

.invite-card {
  overflow: hidden;
  background: #bcdee7 url("../../Img/marrage-card.jpeg") no-repeat center center
    fixed;
  background-size: cover;
  /* position: fixed; */
  padding: 0px;
  margin: 0px;
  width: 100%;
  height: 90vh;
}
